.sign__form {
  max-width: 882px;
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 60px auto 112px;
  padding: 0;
}

@media all and (min-width: 320px) and (max-width: 767px) {
  .sign__form {
    max-width: 100vw;
    margin: 40px auto 77px;
    padding: 0 8.5vw;
  }
}
